*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

.header{
  width: 100%;
  height: 90px;
  display: block;
  background-color: #222222;
}

.inner-header{
  width: 1000px;
  height: 100%;
  display: block;
  margin: 0 auto;
}

.logo-container{
  height: 100%;
  display: table;
  float: left;
}

.logo-container h1{
  color: white;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 300;
}

.logo-container h1 span{
  font-weight: 600;
  color: #b40000;
}

.nav{
  float: right;
  height: 100%;
}

.nav a{
  height: 100%;
  display: table;
  float: left;
  padding: 0 20px;
}

.nav a:last-child{
  padding-right: 0;
}

.nav a li{
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  color: white;
  font-size: 16px;
}

.picture-me{
  border-radius: 50% !important;
}

.navbar{
  background-color: #222222 !important;
  min-height: 90px;
}

.navbar-toggler{
  border-color: #222222 !important;
}

.navbar h1{
  color: white;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 300;
}

.navbar h1 span{
  font-weight: 600;
  color: #b40000;
}

.active{
  color: #b40000 !important;
}

.logo-link:hover{
  text-decoration: none;
}

.divider-red{
  height: 2px;
  width: 75%;
  background-color: #b40000;
}

.career-text{
  color: gray;
}
